@font-face {
  font-family: 'protofolioicon';
  src:
    url('fonts/protofolioicon.ttf?p2gfmw') format('truetype'),
    url('fonts/protofolioicon.woff?p2gfmw') format('woff'),
    url('fonts/protofolioicon.svg?p2gfmw#protofolioicon') format('svg');
  font-weight: normal;
  font-style: normal;
  font-display: block;
}

[class^="pficon-"], [class*=" pficon-"] {
  /* use !important to prevent issues with browser extensions that change fonts */
  font-family: 'protofolioicon' !important;
  speak: never;
  font-style: normal;
  font-weight: normal;
  font-variant: normal;
  text-transform: none;
  line-height: 1;

  /* Better Font Rendering =========== */
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

