.button {
  background-color: transparent;
  border: none;
  text-decoration: none;
  cursor: pointer;
  outline: none;
  padding: 0;
  height: 1.8rem;
  display: inline-flex;
  align-items: center;
  vertical-align: middle;
  line-height: 1em;
  text-align: center;
  border-radius: 5px;
  transition: 0.2s;
  -webkit-transition: 0.2s;
  -moz-transition: 0.2s;
}

.buttonText {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  padding: 0 6px;
  font-weight: inherit;
  white-space: nowrap;
}

.round {
  height: 2.5rem;
  width: 2.5rem;
  padding: 0;
  border-radius: 50%;
  display: inline-flex;
  justify-content: center;
}

.thick {
  height: 2.8rem;
  padding: 0 5px;
  border-radius: 8px;
  font-weight: 600;
}

.fullWidth {
  width: 100%;
  justify-content: center;
}

.button:active {
  opacity: .7
}

.spacer {
  padding: 0 2px;
}

.text-primary {
  color: var(--text-primary)
}

.text-grey {
  color: var(--input-focused-background);
}

.text-accent {
  color: var(--text-accent-button)
}

.text-error {
  color: var(--text-error)
}

.text-white {
  color: var(--text-white);
}

.solid {
  border-radius: 10px;
}

.fill-primary {
  color: var(--accent);
  background-color: var(--primary-shade);
  padding: 0 2px;
}

.fill-accent {
  background-color: var(--accent);
  color: #fff;
  padding: 0 5px;
}

.fill-accent:hover {
  opacity: .8;
}

.fill-accent:active {
  opacity: .7
}

.text-primary:hover, .text-grey:hover {
  background-color: var(--primary-shade);
}

.text-accent:hover {
  background-color: var(--accent-shade)
}

.text-error:hover {
  background-color: var(--red-shade)
}

.text-white:hover {
  background-color: rgba(255, 255, 255, 0.1);
}

.medium {
  font-size: var(--font-medium);
}

.regular {
  font-size: var(--font-regular);
}

.large {
  height: 2.6rem;
  border-radius: 8px;
}